.post{
    width: 100%;
    -webkit-box-shadow: 0px 3px 8px -2px rgba(0,0,0,0.39);
    -moz-box-shadow: 0px 3px 8px -2px rgba(0,0,0,0.39);
    box-shadow: 0px 3px 8px -2px rgba(0,0,0,0.39);
    border-radius: 15px;
    margin: 25px 0;
}

.postWrapper{
    padding: 5px 20px;
}

.postTop{
    width: 100%;
    margin: 10px 0;
    display: flex;
    align-items: center;
}


.postTop img{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
}

.postTop .postProfileName{
    width:90%;
    margin: 0 12px;
}

.postTop .postProfileName span{
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
}

.postTop .postProfileName p{
    font-size: 12px;
    color: rgb(152, 152, 152);
}

.threeDot{
    cursor: pointer;
}

.postImage{
    margin: 10px -20px ;
}

.postImg .postImage {
    width: 638px;
    
}

.postImage img{
    width: 100%;
    max-height: 600px;
}

.postBottom {
    margin: 10px 0;
}

.postBottom .postLikeCounter{
    font-size: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.postLikeCounter img{
    width: 25px;
    cursor: pointer;
    margin-right:5px;
}

.postLikeCounter span{
    width: 70%;
}

.postLikeCounter span strong{
    cursor: pointer;
}
.postLikeCounter p{
    width: 18%;
    cursor: pointer;

}



