.share{
    width: 100%;
    -webkit-box-shadow: 0px 3px 8px -2px rgba(0,0,0,0.39);
    -moz-box-shadow: 0px 3px 8px -2px rgba(0,0,0,0.39);
    box-shadow: 0px 3px 8px -2px rgba(0,0,0,0.39);
    border-radius: 15px;
}

.shareWrpper{
    padding: 20px;
}
.shareTop{
    display: flex;
    align-items: center;
    margin: 0 0 10px 0;
}

.shareTop img{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.shareTop input{
    width: 100%;
    padding: 5px;
    border: none;
    font-weight: 500;
}

.shareTop input:focus{
    outline: none;
}

.shareHr{
    margin: 20px 0;
}


.shareBottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.shareIcon{
    display: flex;
    justify-content: space-between;
}

.shareIconBtn{
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-right: 10px;
}

.shareIconBtn .addaphoto{
    color: tomato;
}
.shareIconBtn .label{
    color: blue;
}
.shareIconBtn .location{
    color: green;
}
.shareIconBtn .emojiemotions{
    color: goldenrod;
}

.shareIconBtn span{
    font-size: 15px;
    font-weight: 500;
    margin: 0 5px;
}

.shareButton{
    padding: 5px 12px;
    border: none;
    background-color: green;
    color: white;
    font-weight: 500;
    border-radius: 5px;
    cursor: pointer;
}