.topbarContainer{
    width: 100%;
    height: 50px;
    background-color: #1877f2;
    display: flex;
}

.topbarLeft{
    flex: 2;
    display: flex;
    align-items: center;
    justify-content: center;
}

.topbarLeft .logo{
    font-size: 22px;
    font-family: sans-serif;
    font-weight: bold;
    color: white;
    cursor: pointer;
}

.topbarCenter{
    flex: 5;
    display: flex;
    justify-content: center;
    align-items: center;
}

.topbarCenter .searchBar{
    width: 80%;
    height: 30px;
    background-color: white;
    border-radius: 35px;
    display: flex;
    align-items: center;
    padding-left: 10px;
}

.topbarCenter .searchBar input{
    border: none;
    width: 90%;
}

.topbarCenter .searchBar input:focus{
    outline: none;
}

.topbarRight{
    flex: 3;
    display: flex;
    justify-content: center;
    align-items: center;
}

.topbarRight .menuBtn{
    display: flex;
    justify-content: space-around;
    flex: 3;
    color: white;
    font-size: 15px;
}

.topbarRight .menuBtn .Home{
    cursor: pointer;
}

.topbarRight .menuBtn .Timeline{
    cursor: pointer;
}

.topbarRight .notificationBtn{
    display: flex;
    justify-content: space-around;
    flex: 3;
    color: white;
}

.topbarRight .notificationIcon {
    position: relative;
    cursor: pointer;

}

.notificationBadge{
    position: absolute;
    top: -5px;
    right: -5px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    color: white;
    font-size: 10px;
    background-color: red;
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile{
    flex: 1;

    display: flex;
    justify-content: center;
    align-items: center;
}

.profile img{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    border: 1.8px solid white;
    object-fit: cover;
    cursor: pointer;
}