.rightbar{
    flex: 3;
    height: calc(100vh - 50px);
    overflow-y: scroll;
}

.rightbar::-webkit-scrollbar{
    width: 0;
}

.rightbarWrapper{
    margin: 25px 0;
}

.rightbarAd{
    width: 100%;
}

.rightbarAd img{
    width: 320px;
    border-radius: 15px;
    margin-bottom: 10px;
    cursor: pointer;
}

.rightbar hr{
    width: 90%;
    margin: 10px 0;
}

.rightbarFriend{
    display: flex;
    align-items: center;
    margin: 20px 0;
    position: relative;
}

.rightbarFriendImg img{
    width: 32px;
    height: 32px;
    object-fit: cover;
    margin: 0 10px 0 0;
    border-radius: 50%;
    cursor: pointer;
}

.rightbarFriendImg span{
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: green;
    top:1;
    left:22px ;
    border: 1.5px solid white;
    cursor: pointer;

}

.rightbarFriend .rightbarFriendName{
    font-weight: 500;
    cursor: pointer;
}