.sidebar{
    flex: 2;
    height: calc(100vh - 50px);
    overflow-y: scroll;
}

.sidebar::-webkit-scrollbar{
    width: 5px;
}
.sidebar::-webkit-scrollbar-track{
    background-color: #f1f1f1;
}
.sidebar::-webkit-scrollbar-thumb{
    background-color: #e4e4e4;
}

.sidebarWrapper{
    padding: 5px 30px;
}

.sidebarAlign{
    display: flex;
    margin: 15px 0;
    cursor: pointer;
}

.sidebarAlign span{
    margin-left: 15px;
    font-size: 15px;
    font-weight: 500;
}

.sidebarBtn{
    display: flex;
    margin: 20px 0 20px 0;
}

.sidebarBtn button{
    padding: 5px 30px;
    border: none;
    font-size: 12px;
    font-weight: 500;
    cursor: pointer;
}

.sidebarBtn button:focus{
    outline: none;
}

